@import url('https://fonts.googleapis.com/css2?family=Arima+Madurai:wght@100;200;300;400;500;700&family=Fugaz+One&family=Noto+Sans:ital,wght@0,300;0,400;1,200;1,300;1,400&family=Work+Sans:wght@200;300;400&display=swap');

/*fonts*/

@font-face{
    font-family: "Finesse";
    src: local("Finesse"),
    url("./fonts/Finesse-Italic.otf") format("opentype");
    font-style: italic;
}

@font-face{
    font-family: "Finesse";
    src: local("Finesse"),
    url("./fonts/Finesse-Roman.otf") format("opentype");
    font-style: normal;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Bold.ttf") format("truetype");
    font-weight: 500;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-SemiBold.ttf") format("truetype");
    font-weight: 400;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Regular.ttf") format("truetype");
    font-weight: 300;
}

@font-face{
    font-family: "Manrope";
    src: local("Manrope"),
    url("./fonts/Manrope-Light.ttf") format("truetype");
    font-weight: 200;
}

body {
    background-color: #f5f5f5;
    font-family: 'Manrope';
    font-weight: 300;
    overflow-x: hidden;
    color: rgb(65, 65, 65);
}

.tracks {
    font-size: 8px;
    font-weight: 200;
}

.button_1{
    
    background-color: none;
    border-radius: 20px;
    border: 1px solid #89c9b4;
    color: rgb(65, 65, 65);
    font-family: 'Work Sans';
    width: 180px;
    height: 30px;
    font-size: 14px;
    margin: auto;
}

.container{
    display: flex;
    flex-flow: row wrap;
    padding: 0px 15px;
    justify-content: center;
}

.footer{
    width: 100%;
    text-align: center;
    font-family: 'Noto Sans';
    font-weight: 400;
    font-size: 10px;
    padding-top: 100px;
    padding-bottom: 20px;
}

.logo{
    width: 40px;
    padding: 20px;
}

.logo-bar{
    width: 100%;
    display: flex;
    justify-content: center;
    height: 70px;
    margin-bottom: 50px;
    
}

.l-text{
    width: 100%;
    text-align: left;
    font-size: 36px;
    font-weight: 100;
    font-family: 'Manrope';
    letter-spacing: 3px;
    margin-bottom: 20px;
    line-height: 28px;
}

.m-text{
    width: 100%;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Noto Sans';
}

.s-text{
    width: 100%;
    text-align: left;
    font-size: 12px;
    font-family: 'Noto Sans';
    font-weight: 400;
}

.main-img{
    width: 50%;
}

.loader{
    width: 100%;
    text-align: center;
    margin: 200px 0px;
}

.menu{
    width: 80%;
}

.menu-button{
    position: absolute;
    top: 10px;
    padding-top: 25px;
    right: 30px;
    font-size: 12px;
    font-family: 'Noto Sans';
    font-weight: 500;
    z-index: 10;
}

.title{
    font-size: 48px;
    font-weight: 200;
    padding-left: 20px;
    padding-top: 100px;
    padding-bottom: 200px;
    font-family: 'Finesse';
    font-style: italic;
    line-height: 1.2;
    letter-spacing: 3px;
    color: rgb(65, 65, 65);
}

@keyframes slidein {
    from {width: 10%}
    to {width: 40%}
}

@keyframes albumclicked {
    from {width: 180px}
    to {width: 200px}
}

.menu-clicked{ /* show menu */
    display: "";
    width: 40%;
    background-color: white;
    padding-right: 0px;
    padding-top: 0px;
    position: absolute;
    right: 0px;
    top: 0px;
    height: 100%;  /* fix this!! */
    animation: slidein 0.5s;

    font-family: 'Noto Sans';
    font-weight: 400;
    color:rgb(65, 65, 65);
    text-decoration: none!important;
    z-index: 5;
}


.menu-nclicked{
    display: none;
}

.album-container{
    float:left;
    padding: 20px 20px 10px 0px;
    height: 200px;
}

.album-img{
    width: 180px;
}

.album-img-clicked{
    width: 200px;
    animation: albumclicked 0.5s;
}

.album-title{
    padding-top: 20px;
    padding-left: 10px;
    font-family: 'Noto Sans';
    font-weight: 400;
    font-size: 14px;
    display: block;
}

.album-detail{
    padding-top: 10px;
    padding-left: 10px;
    font-family: 'Noto Sans';
    font-weight: 200;
    font-size: 14px;
    display: block;
}

.break{
    flex-basis: 100%;
    height: 0;
}

.home-album-container{
    overflow-x: scroll;
    width: 100%;
    display: flex;
    flex-direction: row;
}

.tracklist-container{
    margin-top: 20px;
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    width: 100%;
    box-shadow: 1px 1px 5px rgb(204, 202, 202);

}

.preview-clicked{
    position: absolute;
    height: 350px;
    width: 100%;
    top: 150px;
    right: 0px;
    z-index: 1;
    display: flex;
    justify-content: center;
}

.preview-nclicked{
    display: none;
}

.preview-video{
    margin: 0px 20px;
    height: 370px;
    width: 100%;
    background-color: rgb(204, 202, 202);
    border-radius: 10px;
}

.preview-text{
    z-index: 2;
    position: absolute;
    top: 315px;
    text-align: center;
    color: white;
    font-family: 'Fugaz One';
    font-size: 36px;
    font-weight: 400;
    line-height: 28px;
    opacity: 0.7;
    padding: 0px 40px;
}

.preview-text-s{
    z-index: 2;
    position: absolute;
    top: 20px;
    text-align: center;
    color: white;
    font-family: 'Noto Sans';
    font-size: 10px;
    font-weight: 200;
    opacity: 0.7;
    padding: 0px 40px;
}

/*scrollbar*/
/* width */
::-webkit-scrollbar {
    height: 5px;
    
  }
  
::-webkit-scrollbar-track {
    margin-top: 10px 0px;
    background-color: none;
  }
  /* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(204, 202, 202);
    border-radius: 5px;
  }


  